header {
  position: fixed;
  z-index: 999;
  top: 0;
  left: 0;
  right: 0;
  height: 70px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 30px;
  background: transparent;
  transition: all ease 0.6s;
}

.header-preto {
  background-color: #141414;
}

.header-logo {
  height: 25px;
}

.header-logo img,
.header-usuario img {
  height: 100%;
}

.header-usuario {
  height: 35px;
}

.header-usuario img {
  border-radius: 4px;
}
